export default function noise() {
  let canvas: HTMLCanvasElement, ctx: CanvasRenderingContext2D;
  let wWidth: number, wHeight: number;
  let noiseData: ImageData[] = [];
  let frame = 0;
  let loopTimeout: number;
  const createNoise = () => {
    const imgData = ctx.createImageData(wWidth, wHeight);
    const buffer32 = new Uint32Array(imgData.data.buffer);
    const len = buffer32.length;
    for (let i = 0; i < len; i++) {
      if (Math.random() < 0.5) {
        buffer32[i] = 0xff000000;
      }
    }
    noiseData.push(imgData);
  };
  const paintNoise = () => {
    if (frame === 9) {
      frame = 0;
    } else {
      frame++;
    }
    ctx.putImageData(noiseData[frame], 0, 0);
  };
  const loop = () => {
    paintNoise();
    if (document.body.classList.contains("chaos")) return;
    loopTimeout = window.setTimeout(() => {
      window.requestAnimationFrame(loop);
    }, 1000 / 25);
  };
  const setup = () => {
    wWidth = window.innerWidth;
    wHeight = window.innerHeight;
    canvas.width = wWidth;
    canvas.height = wHeight;
    for (let i = 0; i < 10; i++) {
      createNoise();
    }
    loop();
  };
  let resizeThrottle: number;
  const init = () => {
    canvas = document.getElementById("noise") as HTMLCanvasElement;
    ctx = canvas.getContext("2d") as CanvasRenderingContext2D;
    ctx.clearRect(0, 0, canvas.width, canvas.height);
    setup();
  };
  init();
  window.addEventListener(
    "resize",
    () => {
      window.clearTimeout(resizeThrottle);

      resizeThrottle = window.setTimeout(() => {
        window.clearTimeout(loopTimeout);
        init();
      }, 200);
    },
    false
  );
}
