import * as Tone from "tone";

declare global {
	interface Window {
		preventNote: boolean;
	}
}

const colors = ["#fff", "#ff0", "#3cf", "#f3c", "#3f3"];
function getColor() {
	return colors[Math.floor(Math.random() * colors.length)];
}
const chars = [
	["", "", "", "", "", "", "", "", "", ""],
	["", "", "", "", "", "", "", "", "", ""],
	["", "", "", "", "", "", "", "", "", ""],
	["", "", "", "", "", "", "", "", "", ""],
	["", "", "", "", "", "", "", "", "", ""],
];
const notes = [
	["C4", "D4", "E4", "F4", "G4", "A4", "B4", "C5", "D5", "E5"],
	["E3", "G3", "B3", "D4", "E4", "G4", "B4", "D5", "E5", "G5"],
	["G3", "B3", "D4", "F4", "G4", "B4", "D5", "F5", "G5", "B5"],
	["C3", "E3", "G3", "B3", "C4", "E4", "G4", "B4", "C5", "E5"],
	["D3", "F3", "A3", "C4", "D4", "F4", "A4", "C5", "D5", "F5"],
];
let lastNoteClass = -1;
function getCharsNotes() {
	const r = Math.floor(Math.random() * 10);
	lastNoteClass = (lastNoteClass + 1) % notes.length;
	return { note: notes[lastNoteClass][r], char: chars[lastNoteClass][r] };
}

const MAX_NOTES = 100;

export default function tuner() {
	const synth = new Tone.PolySynth(Tone.Synth).toDestination();
	synth.volume.value = -10;

	let isDrawing = false;
	let drawnCells: Record<string, { note: string; char: string }> = {};

	function initCanvas() {
		drawnCells = {};
		document.querySelectorAll("notes").forEach((el) => el.remove());
	}

	function getId(x: number, y: number) {
		return `c${x}-${y}`;
	}

	function getCoords(id: string) {
		id = id.replace("c", "");
		const coords = id.split("-");
		return [Number(coords[0]), Number(coords[1])];
	}

	let timeout: any;

	function drawCell(x: number, y: number, auto = false) {
		if (timeout) return;
		if (!auto && window.preventNote) return;
		timeout = setTimeout(() => {
			timeout = null;

			const { char, note } = getCharsNotes();

			if (!drawnCells[getId(x, y)]) {
				synth.triggerAttackRelease(note, "8n");
				drawnCells[getId(x, y)] = { char, note };
				const span = document.createElement("span");
				span.id = getId(x, y);
				span.className = "note";
				span.textContent = char;
				span.style.color = getColor();
				span.style.left = x - 2 + "px";
				span.style.top = y + "px";
				document.body.appendChild(span);
			}

			const cellKeys = Object.keys(drawnCells);
			const totalNotes = cellKeys.length;

			if (totalNotes > MAX_NOTES) {
				for (let i = 0; i < totalNotes - MAX_NOTES; i++) {
					const [x, y] = getCoords(cellKeys[i]);
					const span = document.getElementById(getId(x, y));
					if (span) {
						span.remove();
						delete drawnCells[cellKeys[i]];
					}
				}
			}

			const playBtn = document.getElementById("play-composition");

			if (playBtn) {
				if (totalNotes > 1) {
					playBtn.classList.add("visible");
				} else {
					playBtn.classList.remove("visible");
				}
			}
		}, 100);
	}

	function playComposition() {
		if (Object.keys(drawnCells).length === 0) return;

		const now = Tone.now();
		let timeOffset = 0;

		Object.keys(drawnCells).forEach((key) => {
			const cell = drawnCells[key];
			const [x, y] = getCoords(key);

			synth.triggerAttackRelease(cell.note, "8n", now + timeOffset);

			const span = document.getElementById(getId(x, y));
			if (span) {
				setTimeout(() => {
					span.classList.add("highlight");
					setTimeout(() => {
						span.classList.remove("highlight");
					}, 50);
				}, timeOffset * 1000);
			}

			timeOffset += 0.2;
		});
	}

	document.addEventListener("compose", function (e: CustomEventInit<number>) {
		const noteCount = e.detail || 100;
		window.preventNote = true;
		let playedNote = 0;
		let int = setInterval(() => {
			playedNote++;
			if (playedNote >= noteCount) {
				clearInterval(int);
				window.preventNote = false;
			} else {
				const x = Math.floor(Math.random() * window.innerWidth);
				const y = Math.floor(Math.random() * window.innerHeight);
				drawCell(x, y, true);
			}
		}, 150);
	});

	window.addEventListener("composition", playComposition);
	window.addEventListener("clear", function () {
		Object.keys(drawnCells).forEach((cell) => {
			document.querySelector("#" + cell)?.remove();
		});
	});

	document.body.addEventListener("mousedown", (e: MouseEvent) => {
		const target = e.target as HTMLElement;
		if (["BUTTON", "A"].includes(target.tagName)) return;

		isDrawing = true;
		drawCell(e.clientX, e.clientY);
	});

	document.body.addEventListener("mousemove", (e) => {
		if (isDrawing) {
			drawCell(e.clientX, e.clientY);
		}
	});

	document.addEventListener("mouseup", () => {
		isDrawing = false;
	});

	document.body.addEventListener("touchstart", (e) => {
		e.preventDefault();
		isDrawing = true;
		const touch = e.touches[0];
		drawCell(touch.clientX, touch.clientY);
	});

	document.body.addEventListener("touchmove", (e) => {
		e.preventDefault();
		if (isDrawing) {
			const touch = e.touches[0];
			drawCell(touch.clientX, touch.clientY);
		}
	});

	document.body.addEventListener("touchend", () => {
		isDrawing = false;
	});

	window.addEventListener("resize", () => {
		setTimeout(initCanvas, 100);
	});

	document.body.addEventListener(
		"click",
		() => {
			if (Tone.getContext().state !== "running") {
				Tone.getContext().resume();
			}
		},
		{ once: true }
	);

	initCanvas();
}
