import React, { Suspense, lazy, useEffect, useRef, useState } from "react";

import noise from "../scripts/noise-machine";
import socials from "../data/socials";
import { useFollowPointer } from "../hooks/usePointerFollower";
import { motion } from "framer-motion";
import { Terminal } from "../components/Terminal";
import tuner from "../scripts/tuner-machine";

const ThreeDS = lazy(() => import("../components/ThreeDS"));

let interval: NodeJS.Timeout | number = 0;

export default function Home() {
	const [modal, setModal] = useState(false);
	const [progress, setProgress] = useState(-200);
	const [isDown, setIsDown] = useState(false);
	const cursor = useRef<HTMLDivElement | null>(null);
	const follow = useRef<HTMLDivElement | null>(null);
	const { x: cX, y: cY } = useFollowPointer(cursor);
	const { x: fX, y: fY } = useFollowPointer(follow, 1);

	useEffect(() => {
		const navLinks = document.querySelectorAll(
			"a, button, .intro-details strong"
		);
		const addTransformer = () => {
			if (cursor.current) {
				cursor.current.classList.add("link");
			}
			if (follow.current) {
				follow.current.classList.add("link");
			}
		};
		const removeTransformer = () => {
			if (cursor.current) {
				cursor.current.classList.remove("link");
			}
			if (follow.current) {
				follow.current.classList.remove("link");
			}
		};
		for (let i = 0; i < navLinks.length; i++) {
			navLinks[i].addEventListener("mouseenter", addTransformer);
			navLinks[i].addEventListener("mouseleave", removeTransformer);
		}

		noise();
		tuner();

		window.onresize = () => {
			noise();
		};
	}, []);

	useEffect(() => {
		if (!isDown) {
			clearInterval(interval);
			setProgress(-200);
			return;
		}
		if (progress >= 25) {
			setProgress(-200);
			setIsDown(false);
			clearInterval(interval);
			setModal(false);
			window.open(
				String.fromCharCode(
					83,
					104,
					97,
					107,
					105,
					108,
					45,
					67,
					86,
					46,
					112,
					100,
					102
				)
			);
			return;
		}
		setTimeout(() => {
			setProgress(progress + 1);
		}, 5);
	}, [progress, isDown]);

	function handleModal() {
		setModal(!modal);
	}
	function pressStart() {
		setIsDown(true);
	}
	function pressStop() {
		setIsDown(false);
		setProgress(-200);
		clearInterval(interval);
	}

	function playComposition() {
		window.dispatchEvent(new Event("composition"));
	}
	return (
		<>
			<motion.div ref={cursor} id="cursor" style={{ x: cX, y: cY }} />
			<motion.div ref={follow} id="follow" style={{ x: fX, y: fY }} />
			<canvas id="noise"></canvas>
			<Suspense fallback={null}>
				<ThreeDS />
			</Suspense>
			<main>
				<div className="intro">
					<div className="intro-title">
						<h1>
							Hello<span>.</span>
						</h1>
						<h2>
							I am Shak
							<span className="red">!</span>l
						</h2>
						<div className="lr">
							Full Stack <strong>WEB</strong> and{" "}
							<strong>MOBILE APPLICATION</strong> Developer
						</div>
					</div>
					<div className="intro-details">
						<p className="secondary">
							My area of expertise primarily includes{" "}
							<strong className="react">React</strong>
							<small> (w/ Gatsby, Next)</small>, and{" "}
							<strong className="react">React Native</strong>. I have a
							professional background in{" "}
							<strong className="mui">Material UI</strong>,{" "}
							<strong className="semantic-ui">Semantic UI</strong>,{" "}
							<strong className="tailwind">Tailwind CSS</strong>,{" "}
							<strong className="laravel">Laravel</strong>,{" "}
							<strong className="node">Node.js</strong>,{" "}
							<strong className="mysql">
								<span>My</span>
								<span>SQL</span>
							</strong>
							, <strong className="mongo">MongoDB</strong>, and server
							managements
							<small>
								{" "}
								(w/ <strong className="aws">AWS</strong>,{" "}
								<strong className="nginx">Nginx</strong>,{" "}
								<strong className="apache">Apache</strong>, and{" "}
								<strong className="litespeed">LiteSpeed</strong>)
							</small>
							.
						</p>
					</div>
				</div>
				<div className="intro-details">
					<p className="big">
						If you have any <strong>project</strong> request,{" "}
						<strong>query</strong>, or an <strong>idea</strong> to discuss, feel
						free to <a href="mailto:hi@shakil.info">say hi</a>!
					</p>
				</div>
				<div className="socials">
					<button
						className="tb-destroyed"
						title="Download Rsum"
						onClick={handleModal}
					>
						<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 384 512">
							<path
								fill="currentColor"
								d="M64 0C28.7 0 0 28.7 0 64V448c0 35.3 28.7 64 64 64H320c35.3 0 64-28.7 64-64V160H256c-17.7 0-32-14.3-32-32V0H64zM256 0V128H384L256 0zM80 64h64c8.8 0 16 7.2 16 16s-7.2 16-16 16H80c-8.8 0-16-7.2-16-16s7.2-16 16-16zm0 64h64c8.8 0 16 7.2 16 16s-7.2 16-16 16H80c-8.8 0-16-7.2-16-16s7.2-16 16-16zm54.2 253.8c-6.1 20.3-24.8 34.2-46 34.2H80c-8.8 0-16-7.2-16-16s7.2-16 16-16h8.2c7.1 0 13.3-4.6 15.3-11.4l14.9-49.5c3.4-11.3 13.8-19.1 25.6-19.1s22.2 7.7 25.6 19.1l11.6 38.6c7.4-6.2 16.8-9.7 26.8-9.7c15.9 0 30.4 9 37.5 23.2l4.4 8.8H304c8.8 0 16 7.2 16 16s-7.2 16-16 16H240c-6.1 0-11.6-3.4-14.3-8.8l-8.8-17.7c-1.7-3.4-5.1-5.5-8.8-5.5s-7.2 2.1-8.8 5.5l-8.8 17.7c-2.9 5.9-9.2 9.4-15.7 8.8s-12.1-5.1-13.9-11.3L144 349l-9.8 32.8z"
							/>
						</svg>
					</button>
					{socials.map(({ a, icon }) => (
						<a
							key={a.title}
							href={a.href}
							title={a.title}
							rel="noopener noreferrer"
							target="_blank"
							className="tb-destroyed"
						>
							{icon}
						</a>
					))}
				</div>
				<div className="scroll-bar">
					<div className="marquee">
						<p>
							{[0, 1, 2].map((_, idx) => (
								<span key={idx}>
									&nbsp;<b>UI</b>/<b>UX</b>
									&nbsp;DESIGNER&nbsp;|&nbsp;DEVELOPER&nbsp;|&nbsp;FREELANCER&nbsp;|&nbsp;CONSULTANT&nbsp;|
								</span>
							))}
						</p>
					</div>
				</div>
				<div className={`modal ${modal ? "opened" : ""}`}>
					<div
						className="modal-backdrop"
						role="button"
						tabIndex={0}
						onClick={handleModal}
						onKeyDown={handleModal}
					>
						{" "}
					</div>
					<div className="modal-grad" />
					<button
						className="modal-content"
						onMouseDown={pressStart}
						onMouseLeave={pressStop}
						onBlur={pressStop}
						onMouseUp={pressStop}
						onTouchStart={pressStart}
						onTouchEnd={pressStop}
					>
						{isDown ? (
							<p>opening rsum</p>
						) : (
							<p>
								press and hold
								<br />
								to download
							</p>
						)}
						<div
							className="mask"
							style={{ transform: "translateY(" + (-100 + progress) + "px)" }}
						/>
					</button>
				</div>
			</main>
			<Terminal />
			<button onClick={playComposition} id="play-composition">
				<svg
					xmlns="http://www.w3.org/2000/svg"
					width="16"
					height="16"
					fill="currentColor"
					viewBox="0 0 16 16"
				>
					<path d="M12 13c0 1.105-1.12 2-2.5 2S7 14.105 7 13s1.12-2 2.5-2 2.5.895 2.5 2" />
					<path fillRule="evenodd" d="M12 3v10h-1V3z" />
					<path d="M11 2.82a1 1 0 0 1 .804-.98l3-.6A1 1 0 0 1 16 2.22V4l-5 1z" />
					<path
						fillRule="evenodd"
						d="M0 11.5a.5.5 0 0 1 .5-.5H4a.5.5 0 0 1 0 1H.5a.5.5 0 0 1-.5-.5m0-4A.5.5 0 0 1 .5 7H8a.5.5 0 0 1 0 1H.5a.5.5 0 0 1-.5-.5m0-4A.5.5 0 0 1 .5 3H8a.5.5 0 0 1 0 1H.5a.5.5 0 0 1-.5-.5"
					/>
				</svg>
				Play Composition
			</button>
		</>
	);
}
